const initialState = {
  loading: false,
  totalSupply: -1,
  cost: 0,
  maxMintAmountPerTx: 0,
  maxMintAmountPerAddress: 0,
  paused: true,
  onlyWhitelisted: true,
  minted: 0,
  error: false,
  errorMsg: "",
};

const dataReducer = (state = initialState, action) => {
  switch (action.type) {
    case "CHECK_DATA_REQUEST":
      return {
        ...state,
        loading: true,
        error: false,
        errorMsg: "",
      };
    case "CHECK_DATA_SUCCESS":
      return {
        ...state,
        loading: false,
        totalSupply: action.payload.totalSupply,
        cost: action.payload.cost,
        maxMintAmountPerTx: action.payload.maxMintAmountPerTx,
        maxMintAmountPerAddress: action.payload.maxMintAmountPerAddress,
        paused: action.payload.paused,
        onlyWhitelisted: action.payload.onlyWhitelisted,
        minted: action.payload.minted,
        error: false,
        errorMsg: "",
      };
    case "CHECK_DATA_FAILED":
      return {
        ...initialState,
        loading: false,
        error: true,
        errorMsg: action.payload,
      };
    default:
      return state;
  }
};

export default dataReducer;
