// log
import store from "../store";

const fetchDataRequest = () => {
  return {
    type: "CHECK_DATA_REQUEST",
  };
};

const fetchDataSuccess = (payload) => {
  return {
    type: "CHECK_DATA_SUCCESS",
    payload: payload,
  };
};

const fetchDataFailed = (payload) => {
  return {
    type: "CHECK_DATA_FAILED",
    payload: payload,
  };
};

export const fetchData = () => {
  return async (dispatch) => {
    dispatch(fetchDataRequest());
    try {
      let totalSupply = await store
        .getState()
        .blockchain.smartContract.methods.totalSupply()
        .call();
      
      let cost = await store
        .getState()
        .blockchain.smartContract.methods.getCost()
        .call();

      let maxMintAmountPerTx = await store
        .getState()
        .blockchain.smartContract.methods.getMaxMintAmountPerTx()
        .call();

      let maxMintAmountPerAddress = await store
        .getState()
        .blockchain.smartContract.methods.getMaxMintAmountPerAddress()
        .call();

      let paused = await store
        .getState()
        .blockchain.smartContract.methods.getPaused()
        .call();

      let onlyWhitelisted = await store
        .getState()
        .blockchain.smartContract.methods.getOnlyWhitelisted()
        .call();

      let minted = await store
        .getState()
        .blockchain.smartContract.methods.numberMinted(store.getState().blockchain.account)
        .call();

      dispatch(
        fetchDataSuccess({
          totalSupply,
          cost,
          maxMintAmountPerTx,
          maxMintAmountPerAddress,
          paused,
          onlyWhitelisted,
          minted
        })
      );
    } catch (err) {
      console.log(err);
      dispatch(fetchDataFailed("Could not load data from contract."));
    }
  };
};
